.Footer {
  height: 40vh;
  grid:
    'footer footer footer footer'
    'footer footer footer footer'
    'footer footer footer footer'
    'footer footer footer footer';
  display: grid;
  background-image: linear-gradient(
    to top right,
    rgba(89, 175, 237, 0.9),
    rgba(55, 122, 172, 0.9)
  );
}

dt {
  font-size: 3rem;
}
dd {
  font-size: 2.5rem;
}
.LeftData {
  margin-top: 0.5rem;
  margin-left: 1rem;
  color: rgb(33, 33, 33);
}
.RightData {
  margin-top: 0.5rem;
  margin-right: 1rem;
}
.RightData a {
  text-decoration: none;
  color: rgb(33, 33, 33);
}
.RightData a:hover {
  text-decoration: underline;
  color: rgb(53, 255, 22);
}

.LeftContact {
  grid-area: 3 / 1;
  text-align: left;
  margin: 1rem;
}
.RightContact {
  grid-area: 3 / 4;
  text-align: right;
  margin: 1rem;
}

.LeftStudent {
  margin-left: 2rem;
  font-weight: bold;
  color: rgb(255, 255, 255);
}
.RightStudent {
  margin-right: 2rem;
  font-weight: bold;
  color: rgb(255, 255, 255);
}

@media (max-width: 40rem) {
  dt {
    font-size: 1.4rem;
    margin-bottom: -0.3rem;
  }
  dd {
    font-size: 1.2rem;
  }
  .Footer {
    min-height: fit-content;
  }
  .LeftContact {
    grid-area: 4 / 1;
    text-align: left;
    margin-bottom: 3rem;
  }
  .RightContact {
    grid-area: 3 / 1;
    text-align: left;
    margin-top: -1rem;
    /* margin-bottom: -1rem; */
  }
  .RightData {
    margin-left: 1rem;
    margin-bottom: -1rem;
  }
  .LeftData {
    margin-bottom: -1rem;
  }
  .RightStudent {
    margin-left: 2rem;
  }
}

@media (max-height: 27rem) {
  .RightContact {
    margin-top: 1rem;
  }
  .LeftContact {
    margin-bottom: 2rem;
  }
  dt {
    font-size: 1rem;
    margin-bottom: -0.3rem;
  }
  dd {
    font-size: 0.8rem;
  }
}
@media (min-height: 43rem) {
  .RightContact {
    margin-bottom: -2rem;
  }
  dt {
    font-size: 1.7rem;
    margin-bottom: -0.3rem;
  }
  dd {
    font-size: 1.3rem;
  }
}
